@font-face {
    font-family: 'digi';
    src: url('http://cssdeck.com/uploads/resources/fonts/digii/DS-DIGII.TTF');
}

* {
    box-sizing: border-box;
}

.countdown {
    width: 200px; height: 68px;
    /* background: white;    */
    font-size: 3em;
    color: #707070;
    
    margin-left: 40px;
    margin-bottom: 10px;

    /* Centering everything */
    /* position: absolute; */
    /* left: 50%; top: 50%; */
    /* margin-left: -125px; margin-top: -50px; */
    
    text-align: center;
    line-height: 60px;
    
    transition: all .3s ease-in;
    border-top: 5px solid #faa346;
    border-bottom: 5px solid #faa346;
    background-image: url(http://localhost:3000/assets/images/link-bg.png);
    background-repeat: repeat-x;
    /* background-image: linear-gradient(#f0f0f0, white); */
}

.timer-off {
    display: none;
}
